:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin-top: 3px;
}

.nav__list {
  margin: 0;
  text-decoration: none;
  display: flex;
}

.nav__link {
  margin-right: 20px;
}

.nav__link:last-child {
  margin-right: 0;
}

.nav__item {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__item:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
